$font_path: '../../fonts/';

@import '../../scss/helpers/helpers-all';
@import '../../scss/reset';
@import '../../scss/helpers/mixins/project-mixins.scss';
@import 'ScssHelpers/helpers-all.scss';

// ---------------------------------------------------------------------------------fonts#
@include font_face('Rubik-Medium', 'Rubik', $font_path, 500);
@include font_face('Rubik-Bold', 'Rubik', $font_path, 700);
// ---------------------------------------------------------------------------------fonts##
// ---- vars#
$point_2: 1024px;
$point_3: 1023px;
$point_6: 480px;
// ---- vars##
// // ---------------------------------------------------------------------------------base#
:root {
	--full-screen: calc(var(--vh, 1vh) * 100);

	--gl-indent: 2.5em;
	--content-width: 135.1em;

	--font_1: 'RobotoCondensed', sans-serif;
	--font-size-body: 1.6em;
	--color-text-base: #000;

	--body-background: #fff;
	--accent-color: #4aa3df;
	--input-placeholder-color: var(--color-text-base);

	@include bp($point_2, min) {
		--font-size-html: 2.5vw;
	}

	@include bp($point_3) {
		--font-size-html: 10px;
	}
}

html {
	height: 100%;

	font-family: var(--font_1);
	font-weight: 400;
	line-height: 1.33;
	font-size: var(--font-size-html);
}

body {
	position: relative;

	height: 100%;

	font-size: var(--font-size-body);
	color: var(--color-text-base);

	-webkit-text-size-adjust: none;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background: var(--body-background);
}

.wrapper {
	position: relative;

	width: 100%;
	display: flex;
	flex-direction: column;
	min-height: 100%;
	height: auto !important;

	overflow-x: hidden;
}

.base {
	width: 100%;
	position: relative;

	flex: 1 0 auto;
}

.section {
	position: relative;

	width: 100%;

	font-size: 10px;

	//emove this demo style
	&.demo_mod {
		@include flex_block(row, nowrap, center, center, center);

		height: var(--full-screen);

		font-size: 4em;
		color: #fff;
		text-align: center;

		background-color: #000;
	}
}

.section_in {
	max-width: var(--content-width);
	width: 100%;
	margin: 0 auto;
}

a {
	text-decoration: none;
	color: var(--accent-color);

	&:hover,
	&:focus {
		text-decoration: underline;
	}
}

.icon {
	fill: currentColor;
	
	&.icon--size_mod {
		width: 100%;
		height: 100%;
	}
}
// ---------------------------------------------------------------------------------base##

//----------------------------------------------------------------------------------header styles#

.header_hp {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 101;

	width: 100%;
	font-size: 10px;
	padding: 2.1em var(--gl-indent);
}

.header_hp_in {
	display: flex;
	align-items: center;

	max-width: var(--content-width);
	width: 100%;
	margin: 0 auto;
}

.header_hp__logo {
	display: block;
	width: 7.3em;
	height: 6.6em;
}

.header_hp__logo_img {
	@include cover_img;
}

.header_hp__menu {
	margin: 0 auto;

	@include bp($point_3) {
		display: none;
	}
}

.header_hp__icons_list {
	display: flex;

	margin-right: -6.4em;
}

.header_hp_icons__item {
	padding-right: 6.4em;
}

.header_hp_icons__link {
	display: flex;
	width: 4.7em;
	height: 4.7em;

	background-color: #ffffff;
	border-radius: 50%;
}

.header_hp_icons__icon {
	display: block;
	width: 2.3em;
	height: 2em;
	margin: auto;
	color: transparent;
}

.header_hp__burger {
	position: relative;
	width: 3.8em;
	height: 2.8em;

	@include bp($point_3) {
		margin-left: auto;
	}
}

.header_hp__burger_decor {
	position: absolute;
	height: 3px;
	right: 0;

	background-color: #ffffff;

	border-radius: .3em;

	&::before {
		content: '';
		position: absolute;
		right: 0;

		width: 100%;
		height: 3px;
		border-radius: .3em;

		background-color: #ffffff;
	}

	&:first-child {
		top: 0;

		width: 100%;

		&::before {
			bottom: -1.7em;
		}
	}

	&:last-child {
		bottom: 0;

		width:80%;

		&::before {
			top: -1.6em;
		}
	}
}

//----------------------------------------------------------------------------------header styles##

// ---------------------------------------------------------------------------------styles for components#

.hero {
	position: relative;

	height: var(--full-screen);
	padding: 2em var(--gl-indent);

	background: linear-gradient(74.79deg, #FF9292 -7.33%, #DE5150 90.21%);

	@include bp($point_2, min) {
		font-size: 0.615vw;
	}

	@include bp($point_3, $point_6) {
		font-size: .5em;
	}

	@include bp($point_6 - 1) {
		font-size: .38em;
	}
}

.hero_in {
	display: flex;
	align-items: center;
	height: 100%;
	max-width: 128.8em;
	width: 100%;
	margin: 0 auto;
}

.hero__wrap {
	width: 100%;
}

.hero_slide__wrap {
	display: flex;
	width: 100%;
}

.hero_slide__img_w {
	position: relative;
	z-index: 5;

	display: block;
	width: 100%;
	height: 100%;	
	flex-shrink: 0;

	@include bp($point_2, min) {
		max-width: 57em;
	}

	@include bp($point_3) {
		position: absolute;
		top: 0;
		right: 0;

		opacity: .9;
	}
}

.hero_slide__img {
	@include contain_img;
}

.hero_slide__content {
	width: 100%;

	@include bp($point_2, min) {
		padding: 4.5em 2em 3em 9.9em;
	}

	@include bp($point_3) {
		position: relative;
		z-index: 10;
	}
}

.hero_slide__uptitle {
	font-size: 2.2em;
	font-family: 'Rubik';
	font-style: normal;
	font-weight: 500;

	letter-spacing: 0.1em;
	text-transform: uppercase;

	color: #FFFFFF;

	&:not(:last-child) {
		margin-bottom: .3em;
	}

}

.hero_slide__title {
	font-family: 'Rubik';
	font-weight: 700;
	font-size: 9.9em;
	line-height: 1;
	text-transform: uppercase;

	color: #FFFFFF;

	&:not(:last-child) {
		margin-bottom: .3em;
	}
}

.hero_slide__price {
	display: flex;
	align-items: center;

	font-family: 'Rubik';
	
	color: #FFFFFF;

	span {
		font-weight: 700;
		font-size: 4.4em;

		&:not(:last-child) {
			padding-right: .7em;
			border-right: 1px solid #fff;
		}
	}

	s {
		padding-left: .9em;
		font-size: 3.1em;

		color: #FFFFFF;

		opacity: 0.7;
	}

	&:not(:last-child) {
		margin-bottom: 3.2em;
	}
}

.hero_slide__btn_w {
	padding: 2em .8em;
	max-width: 19.1em;
	width: 100%;
	border-radius: 5em;

	background-color: #fff;
}

.hero_slide__btn {
	font-family: 'Rubik';
	font-size: 2.2em;
	font-weight: 500;
	line-height: 1;
	text-align: center;
	text-transform: uppercase;

	color: #FF4948;
}

.hero_slide__decors {
	font-size: 10px;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;

	@include bp($point_2, min) {
		width: 50%;
	}
	@include bp($point_3) {
		width: 100%;
	}
}

.hero_slide__decor {
	position: absolute;
	opacity: .1;

	&:nth-child(1) {
		top: 27.7%;
		left: -0.6%;

		width: 9em;
		height: 9em;
	}

	&:nth-child(2) {
		top: 33.7%;
		left: 19.2%;

		width: 5.7em;
		height: 7em;
	}

	&:nth-child(3) {
		top: 64%;
		left: -0.1%;

		width: 5.1em;
		height: 6em;
	}

	&:nth-child(4) {
		top: 13.9%;
		left: 84.9%;

		width: 6.4em;
		height: 6.4em;
	}

	&:nth-child(5) {
		top: 41.7%;
		left: 88.2%;

		width: 10.4em;
		height: 10.4em;
	}

	&:nth-child(6) {
		top: 12.4%;
		left: 23.3%;

		width: 7.4em;
		height: 7.5em;
	}

	&:nth-child(7) {
		top: 6%;
		left: 70.9%;

		width: 7.2em;
		height: 6.5em;
	}

	&:nth-child(8) {
    top: 65.9%;
    left: 18.4%;

    width: 7.8em;
    height: 7.5em;
	}

	&:nth-child(9) {
		top: 75.2%;
		left: 73.2%;

		width: 7em;
		height: 7.3em;
	}

}

// ---------------------------------------------------------------------------------styles for components##
